import jss from 'jss'
import QRCode from 'qrcode'
import merge from 'lodash.merge'
import preset from 'jss-preset-default'
import { generateDeepLink } from '@utorgapp/deeplinks-generator'

import { baseStyles } from '../styles'
import { PresetTypesEnum, StyleNames, WidgetOptions } from '../types'
import { BASE_QR_CODE_SIZE, BASE_TEMPLATE_URL, PRESETS } from '../constants'
import { formatAmount, getCryptoCurrencyDisplayValue } from './currency'

jss.setup(preset())

export const createWidget = async ({
	options,
	attributes,
	customStyles = {},
	presetType = PresetTypesEnum.QrCode,
}: WidgetOptions) => {
	const styles = merge({}, baseStyles, customStyles)
	const { classes } = jss.createStyleSheet<StyleNames>(styles).attach()

	const baseUrl = `${BASE_TEMPLATE_URL}/${PRESETS[presetType]}`
	const deepLink = generateDeepLink(baseUrl, options, attributes)
	const qrCodeUrl = await QRCode.toDataURL(deepLink, {
		width: BASE_QR_CODE_SIZE,
		margin: 0,
	})

	const amount = formatAmount(
		options.paymentAmount ?? options.amount,
		options.paymentAmount ? options.paymentCurrency : undefined
	)
	const currency = options.paymentAmount
		? options.paymentCurrency ?? 'USD'
		: getCryptoCurrencyDisplayValue(options.currency)

	return `
          <div class="${classes.wrapper}">
            <div class="${classes.card} ${classes['amount-card']}">
                <div>
                    <span class="${classes['pay-text']}">Pay</span>
                    <span class="${classes['amount-text']}">${amount}</span>
                </div>
                <div class="${classes.flex}">
                	<div class="${classes['divider-vertical']}"></div>
                    <span class="${classes['currency-text']}">${currency}</span>
                </div>
            </div>
            <div class="${classes.card} ${classes['qr-code-card']}">
                <img class="${classes['qr-code-img']}" src=${qrCodeUrl}>
                <span class="${classes['qr-code-text']}">SCAN THE QR CODE</span>
            </div>
            <div class="${classes['divider-wrapper']}">
                <div class="${classes['divider-hr']}"></div>
                <span class="${classes['divider-text']}">OR</span>
                <div class="${classes['divider-hr']}"></div>
            </div>
           <a
           		href="${deepLink}"
              class=${classes.button}
            >
              tap and pay
            </a>      
          </div>
        `
}
