export function getCryptoCurrencyDisplayValue(currency: string) {
	return currency.split('_')[0]
}

export function formatAmount(
	amount: number,
	currency: string | undefined
) {
	const [locale] = navigator.languages
	if (currency) {
		const parts = new Intl.NumberFormat(locale, { style: 'currency', currency }).formatToParts(amount)
		let currencyPart = ''
		const amountPart = parts.reduce((acc, part) => {
			if (part.type !== 'currency') {
				return acc + part.value
			}
			currencyPart = part.value
			return acc
		}, '')
		return `${amountPart}\u2009${currencyPart}`
	}
	return new Intl.NumberFormat(locale).format(amount)
}
