import React from 'react';
import { Form, Button, ButtonToolbar, Schema, FlexboxGrid, Toggle } from 'rsuite';

import './styles.css';
import './form.less';

const { StringType, NumberType } = Schema.Types;

const model = Schema.Model({
  amount: NumberType(),
  paymentAmount: NumberType(),
  address: StringType().isRequired('Address is required'),
  currency: StringType().isRequired('Currency is required'),
  chain: StringType().isRequired('Chain is required'),
  email: StringType().isEmail('Please enter a valid email address.'),
});

const TextField = React.forwardRef((props, ref) => {
  const { name, label, accepter, ...rest } = props;
  return (
    <Form.Group controlId={`${name}-4`} ref={ref}>
      <Form.ControlLabel>{label} </Form.ControlLabel>
      <Form.Control name={name} accepter={accepter} {...rest} />
    </Form.Group>
  );
});

export const WidgetForm = ({onSubmit}) => {
  const formRef = React.useRef();
  const [formError, setFormError] = React.useState({});
  const [isCrypto, setIsCrypto] = React.useState(false);
  const [formValue, setFormValue] = React.useState({
    amount: 100,
    paymentAmount: 200,
    address: 'address',
    externalId: '',
    email: '',
    merchantId: '',
    postbackUrl: '',
    paymentMethodId: '',
    chain: 'BITCOIN',
    currency: 'BTC',
    paymentCurrency: 'EUR',
  });

  React.useEffect(() => {
    onSubmit(formValue);
  }, [])

  const handleSubmit = () => {
    if (!formRef.current.check()) {
      console.error('Form Error', formError);
      return;
    }

    const {amount, paymentAmount, ...rest} = formValue;

    if (isCrypto) {
      onSubmit({ amount, ...rest })
    } else {
      onSubmit({ paymentAmount, ...rest })
    }
  };

  return (
    <FlexboxGrid style={{ display: "flex", flexGrow: 1, justifyContent: "center"}}>
      <FlexboxGrid.Item colspan={12}>
        <Form
          ref={formRef}
          onChange={setFormValue}
          onCheck={setFormError}
          formValue={formValue}
          model={model}
        >
          {isCrypto ? (
            <TextField name="amount" label="Amount" />
          ) : (
            <TextField name="paymentAmount" label="Payment Amount" />
          )}
          <TextField name="address" label="Address" />
          <TextField name="externalId" label="External id" />
          <TextField name="email" label="Email" />
          <TextField name="merchantId" label="Merchant id" />
          <TextField name="postbackUrl" label="Postback url" />
          <TextField name="paymentMethodId" label="Payment Methods id" />

          <TextField name="chain" label="Chain" />
          <TextField name="currency" label="Currency" />
          <TextField name="paymentCurrency" label="Payment Currency" />

          <ButtonToolbar style={{ justifyContent: 'center' }}>
            <Button appearance="primary" onClick={handleSubmit}>
              Submit
            </Button>
            <Toggle
              size="lg"
              checkedChildren="Crypto"
              unCheckedChildren="Fiat"
              checked={isCrypto}
              onChange={() => setIsCrypto(prev => !prev)}
            />
          </ButtonToolbar>
        </Form>
      </FlexboxGrid.Item>
    </FlexboxGrid>
  );
};
